import React, { useEffect,useState } from "react";
import Newsitem from "./Newsitem";
import Spinner from "./Spinner";
import PropTypes from "prop-types";
import InfiniteScroll from "react-infinite-scroll-component";

const News =(props)=> {
  const [articles, setarticles] = useState([]);
  const [loading, setloading] = useState(true);
  const [page, setpage] = useState(1);
  const [totalResults, settotalResults] = useState(0);


  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }; 

  const  updateNews=async()=> {
    props.setProgress(10)
    let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=e76ccd6f3e5b4844bebf6628fc84e1da&page=${page}&pageSize=${props.pageSize}`;
    
    setloading(true)
    let data = await fetch(url);
    props.setProgress(50)

    let parsedData = await data.json();
    setarticles(parsedData.articles);
    settotalResults(parsedData.totalResults);
    setloading(false);
   
    props.setProgress(100)
  }
 useEffect(() => {
      document.title = `${capitalizeFirstLetter(props.category)}- NewsMonkey `
    updateNews();
    // eslint-disable-next-line
  }, [])
  
 

//  const handalPrevclick = async () => {
//   setpage( page - 1);
//     updateNews();
//   };
//  const  handalNextclick = async () => {
    
//     setpage(page + 1)
//     updateNews();
//   };
 const fetchMoreData = async() => {
   
   let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apikey}&page=${page+1}&pageSize=${props.pageSize}`;
   
   setpage( page + 1)
    
    let data = await fetch(url);
    let parsedData = await data.json();
    setarticles(articles.concat(parsedData.articles))
    settotalResults(parsedData.totalResults)
    
  };


    // console.log("this is render")
    return (
      <>
        <React.Fragment>
          
                <h2 className="text-center " style={{margin:'35px 0px',marginTop: '90px'}}>
                   NewsMonkey -Top In {capitalizeFirstLetter(props.category)}
                 </h2>
            {loading && <Spinner />}
            <InfiniteScroll
              dataLength={articles.length}
              next={fetchMoreData}
              hasMore={articles.length !== totalResults}
              loader={<Spinner />}
            >
              <div className="container">
                
              <div className="row">
                {articles.map((element,index) => {
                    return (
                      <div className="col-md-4" key={index}>
                        
                        <Newsitem
                          title={
                            element.title ? element.title.slice(0, 42) : ""
                          }
                          description={
                            element.description
                              ? element.description.slice(0, 68)
                              : ""
                          }
                          imageUrl={element.urlToImage}
                          newsUrl={element.url}
                          author={element.author}
                          date={element.publishedAt}
                          source={element.source.name}
                        />
                      </div>
                    );
                  })
                }
              </div>
              </div>
              
            </InfiniteScroll>
            
          
        </React.Fragment>
      </>
    );
  
}

News.defaultProps = {
  country: "in",
  pageSize: 8,
  category: "general",
};

News.propTypes = {
  country: PropTypes.string,
  pageSize: PropTypes.number,
  category: PropTypes.string,
};
export default News;